/**
 * Utility Functions
 * Các hàm tiện ích: randomizer, validator, formatter
 */

/**
 * Randomize mảng
 * @param {Array} array - Mảng cần randomize
 * @returns {Array} Mảng đã được randomize
 */
export const shuffleArray = (array) => {
  const shuffled = [...array];
  for (let i = shuffled.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
  }
  return shuffled;
};

/**
 * Random số trong khoảng
 * @param {number} min - Giá trị nhỏ nhất
 * @param {number} max - Giá trị lớn nhất
 * @returns {number} Số ngẫu nhiên
 */
export const randomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

/**
 * Format thời gian (ms) thành chuỗi MM:SS
 * @param {number} milliseconds - Thời gian tính bằng milliseconds
 * @returns {string} Chuỗi thời gian dạng MM:SS
 */
export const formatTime = (milliseconds) => {
  const totalSeconds = Math.floor(milliseconds / 1000);
  const minutes = Math.floor(totalSeconds / 60);
  const seconds = totalSeconds % 60;
  return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
};

/**
 * Validate email
 * @param {string} email - Email cần validate
 * @returns {boolean} True nếu email hợp lệ
 */
export const validateEmail = (email) => {
  const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return re.test(email);
};

/**
 * Validate Google Sheet ID
 * @param {string} sheetId - Google Sheet ID
 * @returns {boolean} True nếu ID hợp lệ
 */
export const validateSheetId = (sheetId) => {
  // Google Sheet ID thường là chuỗi dài chứa chữ và số
  return /^[a-zA-Z0-9_-]+$/.test(sheetId) && sheetId.length > 10;
};

/**
 * Debounce function
 * @param {Function} func - Hàm cần debounce
 * @param {number} wait - Thời gian chờ (ms)
 * @returns {Function} Hàm đã được debounce
 */
export const debounce = (func, wait) => {
  let timeout;
  return function executedFunction(...args) {
    const later = () => {
      clearTimeout(timeout);
      func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
};

/**
 * Throttle function
 * @param {Function} func - Hàm cần throttle
 * @param {number} limit - Thời gian giới hạn (ms)
 * @returns {Function} Hàm đã được throttle
 */
export const throttle = (func, limit) => {
  let inThrottle;
  return function executedFunction(...args) {
    if (!inThrottle) {
      func.apply(this, args);
      inThrottle = true;
      setTimeout(() => (inThrottle = false), limit);
    }
  };
};

/**
 * Class name utility (tương tự clsx)
 * @param {...any} classes - Các class names
 * @returns {string} Chuỗi class names đã được merge
 */
export const cn = (...classes) => {
  return classes.filter(Boolean).join(' ');
};

/**
 * Delay function
 * @param {number} ms - Thời gian delay (ms)
 * @returns {Promise} Promise resolve sau khi delay
 */
export const delay = (ms) => {
  return new Promise((resolve) => setTimeout(resolve, ms));
};

